import React from 'react';
import { Row, Col, Avatar, Button, Modal, Form, Select, Input, message } from 'antd';
import axios from 'axios';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';

let { Option } = Select;

const CreateClientForm = (props) => {

    let { user_id, setCreate, data, setData } = props;
    console.log(data);
    let [locationList, setLocationList] = React.useState([]);
    let history = useHistory();

    const getCurrencyAndLocation = async () => {
        let res = await axios.post('/locationAndCurrency', { user_id: 1 });
        setLocationList(res.data.location);
    }

    const createClient = async (values) => {
        try {
            let res = await axios.post('/createclient', { ...values, user_id });
            setCreate(false);
            if (data) {
                setData({});
                let body = {
                    user_id,
                    type: 'Client',
                    rawclient_id: data.rawclient_id,
                    comment: 'Client'
                }
                await axios.post('/saveorcancilsearchclient', body);
                history.push('/orders/form', { client_id: res.data.data });
            } else {
                history.push('/clients');
            }
            message.success(res.data.message);

        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    React.useEffect(() => {
        getCurrencyAndLocation();
    }, [])

    return (
        <div>
            <Form
                name="basicInformation"
                layout="horizontal"
                onFinish={createClient}
                onFinishFailed={onFinishFailed}
            >
                <Row style={{ justifyContent: 'center' }}>
                    <Col xs={24} sm={24} md={24} lg={20} >
                        <Row gutter={16} align="center">
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Contact Person"
                                    name="contactperson"
                                    initialValue={data ? data.rawclient_contactperson : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Company Name"
                                    name="companyname"
                                    initialValue={data ? data.rawclient_companyname : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Office Number"
                                    name="officenumber"
                                    initialValue={data ? data.rawclient_officenumber : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Alternate Number"
                                    name="alternateofficenumber"
                                    initialValue={data ? data.rawclient_alternateofficenumber : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Address"
                                    name="address"
                                    initialValue={data ? data.rawclient_address : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="State"
                                    name="state"
                                    initialValue={data ? data.rawclient_state : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="City Office Number"
                                    name="city"
                                    initialValue={data ? data.rawclient_city : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={12}>
                                <Form.Item
                                    label="Country"
                                    name="country"
                                    rules={[{
                                        required: true,
                                        message: 'Select Campaign Location'
                                    }]}
                                >
                                    <Select >
                                        {
                                            locationList.map(item => {
                                                return (
                                                    <Option key={item.location_id} value={item.location_id}>
                                                        {item.location_name}
                                                    </Option>

                                                )
                                            })
                                        }
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Timezone"
                                    name="timezone"
                                    initialValue={data ? data.rawclient_timezone : "-"}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Email"
                                    name="email"
                                    initialValue={data ? data.rawclient_email : ""}
                                    rules={[
                                        {
                                            required: true,
                                            type: "email",
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Alternate Email"
                                    name="alternateemail"
                                    initialValue={data ? data.rawclient_alternateemail : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Website"
                                    name="website"
                                    initialValue={data ? data.rawclient_website : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Company Industry"
                                    name="companyindustry"
                                    initialValue={data ? data.rawclient_companyindustry : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Designation"
                                    name="designation"
                                    initialValue={data ? data.rawclient_designation : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Zipcode"
                                    name="zipcode"
                                    initialValue={data ? data.rawclient_zipcode : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Total Revenue"
                                    name="totalrevenue"
                                    initialValue={data ? data.rawclient_totalrevenue : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Twitter"
                                    name="twitterid"
                                    initialValue={data ? data.rawclient_twitterid : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Facebook"
                                    name="facebookid"
                                    initialValue={data ? data.rawclient_facebookid : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Instagram"
                                    name="instagramid"
                                    initialValue={data ? data.client_instagramid : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={16} >
                                <Form.Item
                                    label="Company Description"
                                    name="companydescription"
                                    initialValue={data ? data.rawclient_companydecription : ""}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Button type="primary" htmlType="submit" style={{ float: 'right' }}>
                            Save
                        </Button>
                    </Col>
                </Row>
            </Form>
        </div >
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(CreateClientForm);