import React from 'react';
import { Card, Table, Tooltip, Button, Col, Row, Radio, Modal } from 'antd';
import {
    EyeOutlined,
} from '@ant-design/icons';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

import CreateClientForm from 'components/clients/CreateClientForm';

const CreateClientModal = ({ showForm, setShowForm, data, setData }) => {
    return (
        <Modal
            title="Create Client"
            visible={showForm}
            centered
            footer={null}
            onCancel={() => setShowForm(false)}
            width={1000}
            destroyOnClose
        >
            <CreateClientForm setCreate={setShowForm} data={data} setData={setData} />
        </Modal>
    );
}

const LeadsList = (props) => {
    let history = useHistory();
    let { user_id, role_id } = props;
    let [loading, setLoading] = React.useState(true);
    let [showForm, setShowForm] = React.useState(false);
    let [type, setType] = React.useState('saved');
    let [data, setData] = React.useState({});
    let [saved, setSaved] = React.useState([]);
    let [cancelled, setCancelled] = React.useState([]);

    const handleModeChange = (e) => {
        let mode = e.target.value
        setType(mode);
    }
    const getLeads = async () => {
        try {
            let res = await axios.post('/saveorcancilclientlist', { user_id, role_id });
            setSaved(res.data.savedata);
            setCancelled(res.data.canceldata);
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.log(error);
        }
    }

    React.useEffect(() => {
        getLeads();
    }, []);

    const columns = [
        {
            title: 'Client Name',
            dataIndex: 'rawclient_contactperson',
            key: 'client_contactperson',
            align: 'center',
        },
        {
            title: 'Company Name',
            dataIndex: 'rawclient_contactperson',
            key: 'rawclient_companyname',
            align: 'center',
        },
        {
            title: 'Client Email',
            dataIndex: 'rawclient_email',
            key: 'rawclient_email',
            align: 'center',
        },
        {
            title: type === 'saved' ? 'Save By' : 'Cancel By',
            dataIndex: 'user_name',
            key: 'rawclient_email',
            align: 'center',
        },
        {
            title: 'Comment',
            dataIndex: 'rawclient_comment',
            key: 'rawclient_comment',
            align: 'center',
        },
        {
            title: 'Action',
            key: 'action',
            align: 'center',
            render: (text, record) => (
                <div>
                    <>
                        <Tooltip title="View">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<EyeOutlined />}
                                size="small"
                                onClick={() => {
                                    setShowForm(true)
                                    setData(record)
                                }}
                            />
                        </Tooltip>
                    </>
                </div >
            )
        },
    ];

    //*********** MAIN RETURN ****************

    return (
        <>
            <CreateClientModal
                showForm={showForm}
                setShowForm={setShowForm}
                data={data}
                setData={setData}
            />
            <Row style={{ justifyContent: 'center' }}>
                <Radio.Group onChange={handleModeChange} value={type} style={{ marginBottom: 8 }}>
                    <Radio.Button value="saved">Saved Clients</Radio.Button>
                    <Radio.Button value="cancelled">Cancelled Clients</Radio.Button>
                </Radio.Group>
                <Col lg={22}>
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={columns}
                            dataSource={type === 'saved' ? saved : cancelled}
                            loading={loading}
                            rowKey='rawclient_id'
                        />
                    </Card>
                </Col>
            </Row>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
});

export default connect(mapStateToProps)(LeadsList);